<template>
  <div class="login-container">
    <el-card header="" class="login-card">
      <el-form @submit.native.prevent="login">
        <el-form-item label="">
          <el-input v-model="model.username"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input type="password" v-model="model.password"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" native-type="submit"></el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
export default {
  name: "login",
  data() {
    return {
      model: {}
    };
  },
  methods: {
    async login() {
      const res = await this.axios.post("/login", this.model);
      localStorage.token = res.data; //token
      // sessionStorage.token = res.data.token; //token
      console.log(res.data);
      this.$router.push("/blogManagerment/deleteBlog");
      this.$message({
        type: "success",
        message: ""
      });
      console.log(localStorage.token);
    }
  }
};
</script>

<style>
.login-card {
  width: 25rem;
  margin: 5rem auto;
}
.login-container {
  min-height: 1400px;
  /* border: 1px solid red; */
}
</style>