<template>
  <div class="panel panel-default container">
    <p class="blogTitle"></p>
    <input type="text" v-model="blogData.title" class="title form-control" />
    <p class="blogIntro"></p>
    <input type="text" v-model="blogData.Intro" class="title form-control" />
    <VueEditor v-model="blogData.markdown" id="edit" useCustomImageHandler></VueEditor>
    <button type="btn" class="btn btn-primary" v-on:click="saveDoc(),routerTo()"></button>
  </div>
</template>

<script>
import { VueEditor } from "vue2-editor";
export default {
  name: "sendBlog",
  components: {
    VueEditor
  },
  data() {
    return {
      blogData: {
        title: "",
        Intro: "",
        tagCloud: [],
        author: "",
        html: "",
        markdown: ""
      }
    };
  },
  methods: {
    saveDoc() {
      //ID
      //
      let url;
      if (this.$route.params.userId) {
        console.log("put");
        url = "/Editblog/" + this.$route.params.userId;
        this.axios.post(url, this.blogData).then(data => {
          console.log(data);
        });
      } else {
        console.log("post");
        url = "/sendBlog";
        this.axios.post(url, this.blogData).then(data => {
          console.log(data);
        });
      }
    },
    //ID
    getBlog(id) {
      this.axios
        .get(`/findBlog/` + id)
        .then(data => {
          this.blogData = data.data;
        })
        .catch(err => {
          console.log(err);
        });
    },
    routerTo() {
      this.$router.push({
        name: "deleteBlog"
      });
    }
  },
  mounted: function() {
    //ID
    if (this.$route.params.userId) {
      this.getBlog(this.$route.params.userId);
    }
  }
};
</script>

<style scoped>
h1 {
  margin: 10px;
}

.blogTitle {
  text-align: left;
}

.blogIntro {
  text-align: left;
}

.title {
  margin-bottom: 10px;
}
.mavon {
  height: 70vh;
}

.container {
  display: inline-block;
  min-height: 90vh;
}

.btn {
  margin: 10px;
  float: right;
}

.card {
  border: 1px solid red;
}
#tag {
  float: left;
}
</style>
