<template>
  <div class="Index">
    <div class="container-fluid" id="body">
      <!--  article1200px -->
      <h1 class="animated fadeInUp" id="TopText"></h1>
      <h2 class="animated fadeInUp">{{IndexData.date}}</h2>
    </div>
    <Mybody />
  </div>
</template>

<script>
import Mynav from "./head/nav";
import Mybody from "./body/Mybody";
export default {
  name: "Myindex",
  data() {
    return {
      IndexData: {
        date: ""
      }
    };
  },
  components: {
    Mynav,
    Mybody
  },
  methods: {
    timeCheck: function() {
      var MyDate = new Date().getHours();
      if (MyDate >= 5 && MyDate <= 10) {
        this.IndexData.date = ",";
      } else if (MyDate >= 11 && MyDate <= 12) {
        this.IndexData.date = "";
      } else if (MyDate >= 13 && MyDate <= 18) {
        this.IndexData.date = "";
      } else if (MyDate >= 19 && MyDate <= 22) {
        this.IndexData.date = "";
      } else if (MyDate >= 23 && MyDate <= 4) {
        this.IndexData.date = ",";
      } else {
        consol.log("");
      }
    }
  },
  mounted: function() {
    this.timeCheck();
  }
};
</script>

<style scoped>
#body {
  background: url("./../assets/zaoshang.jpg");
  position: relative;
  top: -55px;
  background-size: cover;
  height: 100vh;
}

#nav {
  position: absolute;
}

#TopText {
  position: relative;
  top: 30vh;
  font-size: 4rem;
  text-align: center;
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  font-weight: 500;
}

h2 {
  position: relative;
  top: 35vh;
  font-size: 18px;
  font-weight: 550;
  text-align: center;
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  letter-spacing: 0.5vw;
}

.col-md-8 {
  border: 1px solid red;
}

.fadeInUp {
  color: #e8eaed;
  animation-duration: 1.5s; /**/
  animation-delay: 0; /**/
  animation-iteration-count: 1; /**/
  animation-fill-mode: both; /*---;*/
}
</style>
